import constVal from './const.js';
let {
	blockType,
	StorageKey
} = constVal;

const hiddenProperty = (() => { // document[hiddenProperty] 
	let names = [
		'hidden',
		'webkitHidden',
		'mozHidden',
		'msHidden',
	];
	names = names.filter((e) => (e in document));
	return names.length > 0 ? names[0] : false;
})();

const visibilityChangeEvent = (() => {
	if (!hiddenProperty) {
		return false;
	}
	return hiddenProperty.replace(/hidden/i, 'visibilitychange'); // , 
})();

const isFocus = () => {
	if (!hiddenProperty) { // , 
		return true;
	}
	return !document[hiddenProperty];
};

const unit = {
	getNextType() { // 
		const len = blockType.length;
		return blockType[Math.floor(Math.random() * len)];
	},
	want(next, matrix) { // 
		const xy = next.xy; // xy[0]xy[1]
		const shape = next.shape;
		const horizontal = shape[0].length; //
		return shape.every((m, k1) => {
			return m.every((n, k2) => {
				if (xy[1] < 0) { // left
					console.log("auto ")
					return false;
				}
				if (xy[1] + horizontal > 10) { // right
					console.log("auto ")
					return false;
				}
				if (xy[0] + k1 < 0) { // top
					console.log("auto ")
					return true;
				}
				if (xy[0] + k1 >= 20) { // bottom
					console.log("auto ")
					return false;
				}
				if (n) {
					if (matrix[xy[0] + k1][xy[1] + k2]) { //
						return false;
					}
					return true;
				}
				return true;
			})
		});
	},
	isClear(matrix) { // ,
		const clearLines = [];
		// console.log("isclear",matrix)
		matrix.forEach((m, k) => {
			if (m.every(n => !!n)) {
				clearLines.push(k);
			}
		});
		if (clearLines.length === 0) {
			return false;
		}
		return clearLines;
	},
	isOver(matrix) { // , 
		return matrix[0].some(n => !!n);
	},
	subscribeRecord(store) { //  localStorage
		store.subscribe(() => {
			let data = store.getState().toJS();
			if (data.lock) { // , 
				return;
			}
			data = JSON.stringify(data);
			data = encodeURIComponent(data);
			if (window.btoa) {
				data = btoa(data);
			}
			localStorage.setItem(StorageKey, data);
		});
	},
	isMobile() { // 
		const ua = navigator.userAgent;
		const android = /Android (\d+\.\d+)/.test(ua);
		const iphone = ua.indexOf('iPhone') > -1;
		const ipod = ua.indexOf('iPod') > -1;
		const ipad = ua.indexOf('iPad') > -1;
		const nokiaN = ua.indexOf('NokiaN') > -1;
		return android || iphone || ipod || ipad || nokiaN;
	},
	visibilityChangeEvent,
	isFocus,
};

export default unit;
